import navBar from '@/components/nav-bar.vue'
import {
	userTx
} from '@/api/user.js'
import {
	Comission
} from '@/api/comission'
let comi = new Comission();
export default {
	data: () => ({
		msg: '',
		money: '',
		txmoney: 0,
		pid: '',
		zfbname: ''
	}),
	onLoad() {
		try {
			const user = uni.getStorageSync('user');
			this.pid = user.pid
			this.zfbname = user.zfbname
			if (!this.zfbname) {
				uni.reLaunch({
					url: "/pages/index/bindzfb"
				})
			}
			this.getBalance(this.pid)
		} catch (e) {}
	},
	methods: {
		getBalance(pid) {
			uni.showLoading({
				title: "加载中..."
			})
			comi.getnowbalance({
				pid,
			}).then(res => {
				uni.hideLoading();
				this.money = res.result;
			})
		},
		back() {
			uni.switchTab({
				url: "/pages/index/user"
			})
		},
		jump(url) {
			uni.navigateTo({
				url: `/pages/index/${url}`
			})
		},
		blur(e) {
			this.txmoney = e.detail.value
		},
		cashw() {
			this.txmoney = this.money.CommissionSurplus
			console.log('提现申请',this.money)
			if (this.txmoney == 0) {
				this.msg = "账户余额不足"
				return
			}
			this._userTx(this.pid, this.phone, this.txmoney)
		},
		submit() {
			console.log('a',this.txmoney,'b', this.money)
			if (this.txmoney == 0) {
				this.msg = "输入金额不能为空"
				return
			} else if (this.txmoney > this.money) {
				this.msg = "输入金额大于所剩余额"
				return
			}
			this._userTx(this.pid, this.zfbname, this.txmoney)
		},
		_userTx(pid, phone, money) {
			userTx(pid, phone, money).then(res => {
				if (res.code == 200) {
					uni.showToast({
						title: '申请成功待审核'
					})
				}
			})
		}
	},
	components: {
		navBar,
	}
}
